import VueRouter from 'vue-router'
import Vue from 'vue'
//å¼•å…¥ç»„ä»¶
import About from '../views/About'
import Home from '../views/Home'
import Ruizhong from '../views/Ruizhong.vue'

// å°†ç»„ä»¶æŒ‚è½½åœ¨å®ä¾‹ä¸Š
Vue.use(VueRouter)

//åˆ›å»ºå¹¶æš´éœ²ä¸€ä¸ªè·¯ç”±å™¨
const router=new VueRouter({
    routes:[
        {
            path:'/about',
            component:About
        },
        {
            path:'/home',
            component:Home
        },
        {
            path:'/ruizhong',
            component:Ruizhong
        },
    ],
    mode:'history'
})

// å‘å¤–æŠ›å‡ºè·¯ç”±ç»„ä»¶
export default router